export default class Cache<K, T> {
  __cache = new Map<K, T>();
  constructor() {
    this.__cache = new Map();
  }

  getOrInsert(key: K, insertWith: (key: K) => T): T {
    const value = this.__cache.get(key);
    if (value !== undefined) {
      return value;
    }

    const newValue = insertWith(key);
    this.__cache.set(key, newValue);
    return newValue;
  }
}
